import {Component, OnInit} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {sendjobRequest, sendRequestOffer} from '../../home/home/homemutation';
import {Apollo} from 'apollo-angular';

@Component({
  selector: 'app-career-vacancy',
  templateUrl: './career-vacancy.component.html',
  styleUrls: ['./career-vacancy.component.scss']
})
export class CareerVacancyComponent implements OnInit {
  userData: FormGroup;
  fileToUpload: File = null;
  duties = 'We are looking for an experienced and passionate Social Media Specialist to join our creative team. responsible for managing the social media accounts while ensuring the accounts and content shared increase a brand’s visibility and reputation. The successful candidate will be an excellent communicator, a versatile creative writer, and a team player. You will be an active participant in data collection and analyses in order to consistently improve the company’s social media practices over time.';
  requirements = ['Bachelor’s degree in, communications, marketing, or a related field.',
    ' 3 or more years of social media experience including planning and managing content in a corporate, or agency setting.',
    'Excellent written and verbal communication skills and must have a thorough understanding of social media management and strategy.',
    'Experience using various analytics software.'

  ];
  toSend: any;

  handleFileInput(e): void {
    this.fileToUpload = e.target.files[0];
  }

  constructor(private fb: FormBuilder, private apollo: Apollo) {
  }

  submit(): void {
    this.toSend = this.userData.value;
    this.toSend.cv = this.fileToUpload;
    this.send();
  }

  send(): void {
    this.apollo.mutate({
      mutation: sendjobRequest,
      variables: this.toSend,
      context: {
        useMultipart: true
      }
    }).subscribe((response) => {
      alert('we have received your request');
    }, error => alert(error));
  }


  ngOnInit(): void {
    this.createForm();
  }

  createForm() {
    this.userData = this.fb.group({
      job: [2, Validators.required],
      name: ['', Validators.required],
      email: ['', [Validators.pattern('^[a-z0-9._%+-]+@[a-z0-9.-]+\\.[a-z]{2,4}$')]],
      phone: ['', [Validators.minLength(11), Validators.maxLength(11)]],
    });
  }

}
