import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {CareershomeComponent} from './careershome/careershome.component';
import {RouterModule} from '@angular/router';
import {SharedModule} from '../../shared/shared.module';
import {CareerVacancyComponent} from './career-vacancy/career-vacancy.component';
import {UserGuard} from '../../guard/user.guard';

const routes = [
  {path: 'home', component: CareershomeComponent},
  {
    path: 'vacancy/:id', component: CareerVacancyComponent,
    canActivate: [UserGuard]
  },  {
    path: 'vacancy', component: CareerVacancyComponent,
    canActivate: [UserGuard]
  },
  {path: '', redirectTo: 'home'}

];

@NgModule({
  declarations: [CareershomeComponent, CareerVacancyComponent],
  imports: [
    CommonModule, RouterModule.forChild(routes), SharedModule
  ]
})
export class CareersModule {
}
